import React, { useEffect, useState } from "react";
import "./user.scss";
import {
  Person,
  LockOpen,
  AddCard,
  EditNotifications,
} from "@mui/icons-material/";
import { useHistory, useParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { fetchUsers } from "../../redux/user/userSlice";

export default function User() {
  const dispatch = useDispatch();
  const users = useSelector((state) => state.user).users;
  useEffect(() => {
    dispatch(fetchUsers());
  }, []);
  const { userId } = useParams();
  const history = useHistory();
  const [isLoading, setIsLoading] = useState(false);
  const saveUser = () => {
    setTimeout(() => setIsLoading(false), 1000);
    setTimeout(() => history.push("/users"), 1000);
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    setIsLoading(true);
    saveUser();
  };
  return (
    <div className="user">
      <div className="userTitleWrap">
        <h1 className="userTitle"> </h1>
        <button className="userAddButton"></button>
      </div>
      {isLoading ? (
        <div> ...</div>
      ) : (
        <div className="userContainer">
          <ul className="userControlsList">
            <li className="sidebarListItem">
              <div className="sidebarListItemContent">
                <Person className="sidebarIcon" />
                
              </div>
            </li>
            <li className="sidebarListItem">
              <div className="sidebarListItemContent">
                <LockOpen className="sidebarIcon" />
                 
              </div>
            </li>
            <li className="sidebarListItem">
              <div className="sidebarListItemContent">
                <AddCard className="sidebarIcon" />
                 
              </div>
            </li>
            <li className="sidebarListItem">
              <div className="sidebarListItemContent">
                <EditNotifications className="sidebarIcon" />
                
              </div>
            </li>
          </ul>
          <div className="userUpdate">
            <div className="userInfoWrap">
              <img src="" alt="" className="userAvatar" />
              <button className="userUpdateAvatar userAddButton">
                 
              </button>
              <button className="userRemoveAvatar userAddButton">
                 
              </button>
            </div>
            <form className="userUpdateForm">
              <div className="userUpdateFormWrap">
                <div className="userUpdateFormLeft">
                  <div className="userUpdateFormItem">
                    <label></label>
                    <input
                      className="userUpdateFormInput"
                      type="text"
                      placeholder={users ? `${users[userId-1].name}` : 'Name'}
                    />
                  </div>
                  <div className="userUpdateFormItem">
                    <label>Email</label>
                    <input
                      className="userUpdateFormInput"
                      type="email"
                      placeholder={users ? `${users[userId-1].email}` : 'Email'}
                    />
                  </div>
                  <div className="userUpdateFormItem">
                    <label></label>
                    <input
                      className="userUpdateFormInput"
                      type="phone"
                      placeholder={users ? `${users[userId-1].phone}` : ''}
                    />
                  </div>
                  <div className="userUpdateFormItem">
                    <label></label>
                    <input
                      className="userUpdateFormInput"
                      type="text"
                      placeholder={users ? `${users[userId-1].address.city}` : ''}
                    />
                  </div>
                </div>
                <div className="userUpdateFormRight">
                  <div className="userUpdateFormItem">
                    <label> </label>
                    <input
                      className="userUpdateFormInput"
                      type="text"
                      placeholder={users ? `${users[userId-1].username}` : ' '}
                    />
                  </div>
                  <div className="userUpdateFormItem">
                    <label> </label>
                    <input
                      className="userUpdateFormInput"
                      type="date"
                      placeholder=" "
                    />
                  </div>
                  <div className="userUpdateFormItem">
                    <label></label>
                    <input
                      className="userUpdateFormInput"
                      type="text"
                      placeholder={users ? `${users[userId-1].website}` : ''}
                    />
                  </div>
                  <div className="userUpdateFormItem">
                    <label> </label>
                    <input
                      className="userUpdateFormInput"
                      type="text"
                      placeholder={users ? `${users[userId-1].address.zipcode}` : ''}
                    />
                  </div>
                </div>
              </div>
              <button
                className="userSubmitButton userAddButton"
                type="submit"
                onClick={handleSubmit}
              >
                
              </button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
